.confirmation {
  color: #E0ECFF;
    width: 375px;
    background: #0A172B;
    box-sizing: border-box;
    box-shadow: 0px 4px 50px rgba(0, 0, 0, 0.5);
    border-radius: 4px;

    .confirm-title {
      font-size : 22px;
      display : flex;
      justify-content : space-between;
      align-items: center;
      height : 80px;
      padding : 0 24px;
      img {
        cursor : pointer;
      }
    }
    .confirm-legend {
      height: 93px;
      padding : 0 24px;
      background: linear-gradient(180deg, rgba(97, 122, 255, 0.1) 0%, rgba(97, 122, 255, 0) 100%);
    }
    .confirm-body {
      font-size : 14px;
      .confirm-row {
        padding : 0 24px;
        display : flex;
        justify-content : space-between;
        margin-top : 14px;
        .confirm-col {
          color : #93A1B8;
        }
      }
    }
    .confirm-btn {
      display : flex;
      justify-content : center;
      margin: 32px 0 24px;
    }
}