.contract-info {
    margin : 24px 0 0 24px;
    .c-title {
      color : #E0ECFF;
      font-size : 14px;
      height: 40px;
    }
    .c-body {
      display: flex;
      flex-wrap: wrap;
    }
    .c-row {
      font-size: 12px;
      height: 24px;
      display: flex;
      justify-content: space-between;
      width: 50%;
      flex-wrap: wrap;
    }
    .c-value {
      color : #E0ECFF;
      margin-right :24px;
      display: flex;
    }
}