.symbol-info{
  padding : 0 24px 0 48px;
    display : flex;
    flex-direction: column;  

    .main {
      display : flex;
      align-items : center;
      height: 96px;
      justify-content: space-between;

      .s-left {
        display: flex;    
        flex-direction: column;
        
        .symbol-name {
          font-size : 24px;
          color : #E0ECFF;
          align-items: center;
          display: flex;
          img {
            margin-right: 8px;
          }
        }
      }

      .index-area {
        display: flex;    
        align-items: center;
        .index-area-wraper {
          text-align: center;
        }
        .rate {
          display: flex;
          align-items: center;
          img {
            width: 8px;
            margin-right: 4px;
          }
        }
        .rate.down {
          color : #CE5E5E;
        }
        .rate.up {
          color : #59AE99;
        }
        .index-value {
          font-size :24px;
          color : #E0ECFF;
          height: 32px;
        }
        .select-icon {
          margin-left: 8px;
          cursor: pointer;
        }
      }
    }
}