.m-position {
  margin-top : 16px;
    font-size : 14px;
    overflow: auto;
    white-space: nowrap;
    .p-header {
      display : flex;
      justify-content: space-between;
      height: 40px;
      align-items : center;
    }
    .p-body {
      font-size : 12px;
      height : 184px;
      overflow : auto;
    }
    .p-row {
      display: flex;
      justify-content: space-between;
      color : #E0ECFF;
      font-size : 12px;
      align-items: center;
      height: 32px;
    }
    .p-col.red {
      color : #CE5E5E;
    }
    .p-col.green {
      color : #59AE99;;
    }
    .p-col.gray {
      color : #93A1B8;
    }
    .p-col {
      width : 95px;
      margin-right : 16px;
    }
    .p-col.close-position{
      cursor: pointer;
    }
}