
@-webkit-keyframes fadeIn {
  from { opacity: 0; }
    to { opacity: 1; }
}
@keyframes fadeIn {
  from { opacity: 0; }
    to { opacity: 1; }
}
@keyframes fadeOut {
  from { opacity: 1;}
  to { opacity: 0;}
}
@-webkit-keyframes fadeOut {
  from { opacity: 1;}
  to { opacity: 0;}
}
.leverage-bar {
  display : flex;
  flex-direction: column;
  padding: 0 24px 0 48px;
  min-height: 586px;
  .input-area {
    display : flex;
    justify-content: space-between;
    .l-input {
      input {
        font-size: 14px;
      }
    }
    .cutting-line {
      height: 10px;
      margin: auto;
    }
  }
  .quick-input {
    .input-tip {
      margin : 16px 0;
      font-size : 14px;
      visibility: hidden;
      .number {
        color : #DFE9E6;
      }
    }
    .input-tip.show {
      visibility: visible;
    }
    .input-helper {
      display: flex;
      justify-content: space-between;
      margin : 16px 0;
      .close-only {
        width: 108px;
        visibility: hidden;
      }
      .close-only.show {
        visibility: visible;
      }
      .clear {
        cursor: pointer;
      }
    }
  }
  .trade-warn  {
    display: flex;
    visibility: hidden;
    height: 0;
    
    .content {
      font-size: 12px;
      margin-left: 12px;
    }
  }
  .trade-warn.show {
    visibility: visible;
    height: auto;
    opacity: 1; 
    animation: fadeIn 1s;
    padding: 16px 16px;
  }
  .tip {
    font-size: 14px;
    color : #DFE9E6;
    margin : 28px 0px 12px;
  }
  .slider-bar {
    margin: 16px 0;
    height: 16px;
  }
  .trans-summary{
    margin : 16px 0 0px 0px;
    display: none;
    opacity: 0; 
    height: 0;
    animation: fadeOut 1s;

    .trans-title {
      color : #DFE9E6;
      font-size :14px;
    }
    .trans-body {
      margin-top : 12px;
      font-size : 12px;
      .trans-row {
        height : 24px;
        display : flex;
        justify-content: space-between;
      }
    }
  }
  .trans-summary.show {
    display: block;
    height: auto;
    opacity: 1; 
    animation: fadeIn 1s;
  }
  .action {
    margin: 32px 0px;
    .l-checkbox {
      margin-right: 8px;
    }
    .action-button {
      margin-top: 18px;
      display: flex;
      justify-content: center;
    }
  }
}
.leverage-bar.buy {
  .l-button-group {
    .g-button {
      background-color: rgba(89,174,153,0.1);
      width: 75px;
      height: 40px;
      border-radius: 4px;
      border: 1px solid rgba(89,174,153,0);
      color : #DFE9E6;
    }
    .g-button:hover,.g-button.selected {
      border: 1px solid #59AE99;
    }
  }
  .input-area {
    .l-input{
      width: 158px;
      height: 48px;
      border-radius: 2px;
      background: rgba(89, 174, 153, 0.1);
      color : #DFE9E6;
    }
    .cutting-line {
      border: 2px solid rgba(89, 174, 153, 0.1);
    }
  }
  .quick-input {
    .input-helper {
      .clear {
        color: #59AE99 ;
      }
    }
  }
  .trade-warn {
    border: 1px solid rgba(89, 174, 153, 0.1);
    .content {
      color: #959D9B;
    }
  }
  .order-slider {
    .rc-slider-rail {
      background-color: rgba(89, 174, 153, 0.1);
      height: 4px;
      border-radius: 2px;
    }
    .rc-slider-track {
      background: #59AE99;
      border-radius: 1px;
    }
    .rc-slider-handle {
      border-color: #59AE99;
      height: 16px;
      width: 16px;
      background-color: #59AE99;
    }
  }
  .order-slider.close-only, .order-slider.close-position {
    .rc-slider-rail {
      background-color: rgba(206, 94, 94, 0.1);;
      height: 4px;
      border-radius: 2px;
    }
    .rc-slider-track {
      background: #CE5E5E;
      border-radius: 1px;
    }
    .rc-slider-handle {
      border-color: #CE5E5E;
      height: 16px;
      width: 16px;
      background-color: #CE5E5E;
    }
  }
  .l-button.disabled {
    color: rgba(89, 174, 153, 0.4)!important;
    border : 2px solid rgba(89, 174, 153, 0.4)!important;
  }
  .l-button {
    color: #59AE99;
    border: 2px solid #59AE99;
    background-color: transparent;
    height: 56px;
    width: 324px;
    border-radius: 4px;
  }
  .l-button:hover {
    border : 2px solid #2CFFCB;
    color: #2CFFCB;
  }

  .l-checkbox {
    margin-right: 8px;
    .c-checkbox {
      border: 2px solid #959D9B
    }
    .c-checkbox.checked {
      border: 2px solid #59AE99;
      background-color: #59AE99;
    }
  }
  
  .l-toggle.react-toggle--checked .react-toggle-track{
    background: rgba(89, 174, 153, 0.1);
  }
  .l-toggle.react-toggle--checked .react-toggle-thumb {
    background: #59AE99;
  }
  .l-toggle {
    .react-toggle-thumb {
      background: #959D9B;
    }
    .react-toggle-track {
      background-color: rgba(89, 174, 153, 0.1);
    }
  }
}
