
.margin-panel {
  margin: 0 24px;
  background-color: #0A172B;
  border-radius: 4px;
  box-sizing: border-box;
  box-shadow: 0px 4px 50px rgba(0, 0, 0, 0.5);
  .mp-title {
    display: flex;
    justify-content: space-between;
    color: #E0ECFF;
    align-items: center;
    font-size: 22px;
    height: 80px;
    border-bottom: 1px solid #203B60;
    padding: 0 24px;
    img {
      cursor: pointer;
    }
  }
  .mp-body {
    display: grid;
    @media only screen and (min-width : 375px) {
      grid-template-areas: "b-token-list" "mp-main";
      grid-template-columns: auto auto;
    }
    @media only screen and (min-width : 760px) {
      grid-template-areas: "b-token-list mp-main";
      grid-template-columns: 380px 380px;
    }
    .mp-main {
      grid-area: mp-main;
      background: linear-gradient(202.93deg, rgba(97, 122, 255, 0.1) -13.29%, rgba(223, 229, 255, 0) 46.9%, rgba(85, 91, 237, 0.05) 105.84%);
      font-size: 12px;
      padding: 0 24px;
      .mp-input {
        margin: 32px 0 8px 0;
      }
      .main-title {
        margin: 8px 0 32px;
      }
      .quick-help {
        display: flex;
        justify-content: space-between;
      }
      .mp-desc {
        margin: 32px 0;
        .mp-d-row {
          display: flex;
          height: 24px;
          justify-content: space-between;
        }
      }
      .mp-footer {
        display: flex;
        justify-content: center;
        margin-bottom: 32px;
      }
    }
    .b-token-list {
      grid-area: b-token-list;
      font-size: 14px;
      border-right: 1px solid #203B60;
      .mp-col {
        // width: 108px;
        text-align: center;
      }
      .mp-header {
        display: flex;
        justify-content: space-between;
        height: 48px;
        align-items: center;
        padding: 0 24px;
        background: linear-gradient(180deg, rgba(97, 122, 255, 0.1) 0%, rgba(97, 122, 255, 0) 100%);
      }
      .mp-list {
        .mp-row {
          display: flex;
          justify-content: space-between;
          height: 72px;
          align-items: center;
          border-top: 1px solid #203B60;
          padding: 0 24px;
          color: #E0ECFF;
          .mp-col {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 56px;
            img {
              margin-right: 4px;
            }
          }
        }
        .mp-row:last-child {
          border-bottom: 1px solid #203B60;
        }
        .mp-row:hover {
          border: 1px solid rgba(205, 122, 55, 0.5);
          cursor: pointer;
        }
        .mp-row.selected {
          border: 1px solid #CD7A37;
          background: linear-gradient(180deg, rgba(97, 122, 255, 0.1) 0%, rgba(97, 122, 255, 0) 100%);
        }
      }
    }
  }
}