.account {
  border-top: 1px solid #203B60;
  padding : 0 24px 0 48px;
  .a-info {
    display : flex;
    flex-direction: column;
    margin: 24px auto 32px auto;  
    .content {
      .detail-graph {
        display: flex;
        justify-content: space-between;
        align-items: center;
        .detail {  
          .row.title {
            font-size: 14px;
            color: #E0ECFF;
            margin-top: 0;
          }
          .row {
            font-size: 12px;
            margin-top: 16px;
            .value {
              color : #E0ECFF;
              height: 18px;
            }
          }
        }
      }
      .info-footer {
        display: flex;
        justify-content: space-between;
        margin-top: 24px;
      }
    }
  }
}