.sa-main {
  .s-react-tooltip{
    background: #203B60;
    box-shadow: 0px 0px 30px #0A172B;
    // width: 451px;
    display: flex;
    justify-content: center;
  }
  .__react_component_tooltip {
    padding: 0px;
  }
  .__react_component_tooltip::after{
    content : none;
  }
  .__react_component_tooltip.show {   
    opacity: 1;
    position: absolute;
  }
  .__react_component_tooltip.show.place-bottom {
    margin : 0;
  }
}