.symbol-panel{
  width :  352px;
  .sp-main {
    .future-zone-tab{
      margin-top: 24px;
      .width {
        margin: auto;
      }
    }
    .symbols {  
      margin: 24px 0;
      padding: 0 8px;
      max-height: 400px;
      overflow: auto;
      -ms-overflow-style: none;  /* Internet Explorer 10+ */
      scrollbar-width: none;  /* Firefox */
      &::-webkit-scrollbar { 
        display: none;  /* Safari and Chrome */
      }
      .symbol-row {
        height: 74px;
        display: flex;
        align-items: center;
        justify-content: space-between;
        border: 1px solid #203B60;
        padding: 0 16px;
        .ss-name {
          font-size: 16px;
          display: flex;
          width: 50%;
          align-items: center;
          img {
            margin-right: 4px;
          }
        }
        .ss-value {
          width: 50%;
          display: flex;
          justify-content: flex-end;
          .ss-value-detail {
            display: flex;
            font-size: 14px;
            span ,img {
              margin-right: 8px;
            }
          }
        }
      }
      .symbol-row.selected {
        border: 1px solid #CD7A37;
        border-radius: 4px;
      }
      .symbol-row:hover {
        border: 1px solid rgba(205, 122, 55, 0.5);
        cursor: pointer;
        border-radius: 4px;
      }
    }
    .option-symbols {
      color: #E0ECFF;
      font-size: 16px;
      .accordion__heading {
        height: 64px;
        border-bottom: 1px solid rgba(255, 255, 255, 0.15);
        display: flex;
        align-items: center;
        padding-left: 16px;
        cursor: pointer;
        .accordion__button {
          width: 100%;
          height: 100%;
          display: flex;
          align-items: center;
        }
        .accordion__button:after {
          display: inline-block;
          content: '';
          height: 10px;
          width: 10px;
          margin-right: 12px;
          border-bottom: 2px solid currentColor;
          border-right: 2px solid currentColor;
          transform: rotate(-45deg);
          position: absolute;
          right: 0;
          margin-right: 24px;
        }
        .accordion__button[aria-expanded='true']::after, .accordion__button[aria-selected='true']::after {
          transform: rotate(45deg);
        }
      }
      .accordion__panel {
        .option-row {
          height: 56px;
          padding-left: 32px;
          display: flex;
          border: 1px solid #203B60;
          justify-content: space-between;
          align-items: center;
          margin: 0 8px;
          border-radius: 4px;
          img {
            margin: 0 4px;
          }
          .option-name{
            width : 55%;
            display: flex;
            align-items: center;
          }
          .option-value {
            display: flex;
            align-items: center;
            width: 50%;
          }
        }
        .option-row:hover {
          border: 1px solid rgba(205, 122, 55, 0.5);
          cursor: pointer;
        }
      }
    }
  }

}