.m-history {
  margin-top : 16px;
  overflow: auto;
  white-space: nowrap;
  font-size: 12px;
  .h-header {
    display : flex;
    justify-content: space-between;
    height: 40px;
    align-items : center;
    font-size: 14px;
  }
  .h-body {
    color: #E0ECFF;
    height : 184px;
    overflow : auto;
    -ms-overflow-style: none;  /* Internet Explorer 10+ */
    scrollbar-width: none;  /* Firefox */
    &::-webkit-scrollbar { 
      display: none;  /* Safari and Chrome */
    }
    .h-row {
      display: flex;
      align-items: center;
      justify-content: space-between;
      height: 32px;
    }
  }
  .h-col.Sell {
    color : #CE5E5E;
  }
  .h-col.Buy {
    color : #59AE99;;
  }
  .h-col {
    margin-right : 16px;
    width: 100px;
  }
}